/* 미디어 쿼리 추가 (옵션) */
@media (max-width: 768px) {
    .main {
        margin: 7rem 2rem;
    }
    .post {
        margin: 2rem 4rem;
    }
}

.post {
    display: flex;
    flex-direction: column;
    padding: 2rem; /* 상하좌우 여백을 조절 */
    border-radius: 2rem;
    margin: 10% auto; /* auto를 사용하여 좌우 여백을 자동으로 나눔 */
    max-width: 30rem; /* 필요에 따라 최대 넓이를 지정할 수 있음 */
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* 그림자 효과 추가 */
}

.header {
    margin-top: 2rem;
    
}

.menu {
    display: flex;
    flex-direction: row; /* flex-direction을 row로 설정하여 가로로 정렬 */
    align-items: center;
    justify-content: flex-end;
    font-size: 2rem;
}

.menu1 {
    padding-top: 0.15rem;
}

.menu > * {
    margin-left: 1rem; /* 각 요소들 사이에 간격 추가 */
}


.form_body {
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content:space-around;
}
.form_title {
    width: calc(100% - 2.2rem);
    height: 1rem;
	padding: 1rem;
    border: solid 2px #1E90FF;
	border-radius: 5px;
}

.form_textbox {
    width: 100%;
	height: 10rem;
	padding: 1rem;
	box-sizing: border-box;
	border: solid 2px #1E90FF;
	border-radius: 5px;
	font-size: 16px;
	resize: both;
    margin: 2rem auto;
		
}
.form_footer {
    display: flex;
    justify-content: end;
}

.form_footer button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-radius: 5rem;
    border: none;
    width: 4rem;
    padding: 0.3rem 0;
    margin-right: 0.5rem;
    font-size: 1.5rem;
}