.main {
    /* 여러 설정이 필요한 경우에 추가하세요 */
  }

  @media (max-width: 768px) {
    .nav_header img{
        display: none;
      }
      .nav_header a {
        margin-left: 0.3rem;
      }
      .nav_header h1 {
        text-decoration-line: none;
      }
}
  
  .nav {
    position: fixed;
    display: flex;
    height: 4rem;
    background-color: white;
    
    border: 0.1rem lightgray solid;
    top: 0;
    width: 100%;
  }
  
  .nav_header {
    flex-grow: 1;
    display: flex;
  }

  .nav_header a {
    display: flex;
    align-items: center;
    text-decoration-line: none;
  }

  .nav img {
    width: 3.5rem;
    height: 3rem;
    margin-left: 1rem;
  }

  .nav_header h1 {
    margin: 0;
    float: right;
    color: black;
    font-size: 2rem; 
  }
  
  .nav_body {
    padding: 0.3rem;
    display: flex;
    align-items: center;
    flex-grow: 2;
  }
  
  .nav_body .icon_search {
    position: absolute;
    margin: 0rem;
    font-size: 1.5rem;
    margin-left: 0.5rem;
  }

  .icon_pencil {
    padding: 0.5rem;
    color: black;
    font-size: 2rem;
    border-radius: 100%;
  }
  .icon_bell {
    padding: 0.5rem;
    font-size: 1.5rem;
    border-radius: 100%;
    background-color: rgba(0, 0, 0, 0);
    border-color: rgba(0, 0, 0, 0);
  }
  .icon_arrow {
    color: black;
    padding: 0.5rem;
    font-size: 2rem;
    border-radius: 100%;
    background-color: rgba(0, 0, 0, 0);
    border-color: rgba(0, 0, 0, 0);
  }
  
  .nav_body input {
    border: 0.1rem gray solid;
    border-radius: 50rem;
    font-size: 1rem;
    padding-left: 2rem;
    width: 100%;
    height: 2rem;
  }

  .nav_body input:hover {
    border-color: dodgerblue;
  }
  
  .nav_footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-grow: 1;
    justify-content: flex-end;
    padding-top: 0.5rem;
  }

  .nav_list {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 4rem; /* .nav의 높이만큼 여백 추가 */
    right: 0;
    height: auto;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.nav_list__menu {
  padding: 1rem;
}
.nav_list__menu:hover {
  background-color: lightgrey;
}
@media(hover: hover) and (pointer: ciarse) {
  .nav_list__menu:hover {
    background-color: lightgrey;
  }
}

.nav_list__font {
  color: black;
  text-decoration-line: none;
}

  